import {
	get,
	post,
	config    
} from '@/utils/api.js';
export default {
	name: "activityRule",
	props: {
		// 页面来源  
		activityRuleSource: {
			type: String,
			default: '1'
		},
		// 是否展示
		activityRuleIsShow:{
			type: Boolean,
			default: false
		},
		// 是否 开启动画
		isStartAnimation:{
			type: Boolean,
			default: true
		},
	},
	data() {
		return {
			staticUrl: config.staticUrl,
			actAni:'', // 动画所需配置
			opacityAni:'', // 动画所需配置
			actStyle:{
				color:"red"
			}
		}
	},
	methods: {
		// 关闭弹窗
		closeActivityRule(){
			const that = this;
			that.actAni = ''; // 动画所需配置
			that.actAniClose = ''; // 动画所需配置
			that.startAnimation('130%');
			that.opacityAnimation(0);
			setTimeout(function(){
			   that.$emit('activityRuleColse',false);
			},900)
		},
		// 开始动画
		startAnimation(position=0) {
			const that = this;
			// 活动规则 从下向上滑出 
			const actAnimation = wx.createAnimation({
				duration: 500,
				timingFunction: 'ease',
				delay: 0
			});
			actAnimation
				.translateY(position)
				.step();
			that.actAni = actAnimation.export();
		},
		// 蒙层 渐入 渐出
		opacityAnimation(opacityVal=0) {
			const that = this;
			// 活动规则 从下向上滑出 
			const opaAnimation = wx.createAnimation({
				duration: 500,
				timingFunction: 'ease',
				delay: 0
			});
			opaAnimation
				.opacity(opacityVal)
				.step();
			that.opacityAni = opaAnimation.export();
		},
			  
		isStartAnimationFun(e) {
			const that = this;
			that.startAnimation();
			if(that.isStartAnimation){
				that.startAnimation();
				that.opacityAnimation(.6);
			}
		}
		
	}
}
